import ReactTooltip from "react-tooltip";
import "./MoveSet.css";
const movesDescriptions = require("../move-description.json");
const MoveSet = (props) => {
  let moves = props.MoveSet.map((move) => {
    function getCategoryImage(category) {
      return (
        <div className={`category-img`}>
          <img
            className={`move-category-img`}
            src={`${process.env.PUBLIC_URL}/${category}.png`}
            alt={`${category}`}
          ></img>
        </div>
      );
    }
    // move data
    let level = move[0];
    let name = move[1];
    let category = move[3];
    let power = move[4];
    let accuracy = move[5];

    let img = getCategoryImage(category);
    let id = `${props.Name}-${name}`;
    let description = movesDescriptions[`${name}`];
    let tooltip = `${description}`;
    return (
      <div
        key={`${move[0]}-${props.Name}-${move[1]}`}
        className={`${move[2].toLowerCase()}`}
      >
        <div>
          <p className={`move`} data-for={id} data-tip={tooltip}>
            {`Lvl.${level} ${name} `}
            {`${power} ${accuracy} `}
            {img}
          </p>

          <ReactTooltip
            id={id}
            place="bottom"
            multiline={true}
            effect="solid"
          ></ReactTooltip>
        </div>
      </div>
    );
  });

  return (
    <div>
      <p>Lvl Up Move Set</p>
      {moves}
    </div>
  );
};

export default MoveSet;
